<template>
  <section class="businessPage">
    <main-title> </main-title>
    <fade-translate-transition-group 
      tag="div" 
      class="content">
      <article 
        v-for="(paragraph, index) in paragraphs"
        :key="index"
        :data-index="index"
      >
        <h2 :id="paragraph.id">{{ paragraph.title }}</h2>		
        <p v-html="paragraph.text" />		
        <p 
          v-for="(additionalParagraph, counter) in paragraph.additionalParagraphs"
          :key="counter"
          v-html="additionalParagraph" />
      </article>
      <TheLadder :key="paragraphs.length" />
    </fade-translate-transition-group>
  </section>
</template>

<script>
import TheLadder from "@/components/TheLadder";

export default {
  name: "BusinessPage",
  components: {
    TheLadder
  },
  mixins: [],
  props: {
    className: {
      type: String,
      required: false,
      default: ""
    },
    modifier: {
      type: String,
      required: false,
      default: ""
    }
  },
  data: () => ({
    paragraphs: [
      {
        title: " ",
        id: "review",
        text:
          "  <strong>  </strong>  .  <strong> </strong>  ,   <strong> </strong> ,  <strong>  </strong>   <strong>  </strong>."
      },
      {
        title: " ",
        id: "scores",
        text:
          " <strong>  </strong>.       .   ,    <strong> </strong> (  ).  <strong>     </strong>. "
      },
      {
        title: " ",
        id: "market",
        text:
          "   <strong> </strong>,   <strong>  </strong>.  <i>37  </i>. <strong> </strong>   <strong>     </strong>.",
        additionalParagraphs: [
          "    ,    <strong>   </strong>."
        ]
      },
      {
        title: " ",
        id: "team",
        text:
          "     ,   ,  <strong> </strong>   <strong>   </strong>.   <strong>       </strong>   ,   .",
        additionalParagraphs: [
          " <i>  22%</i>,  <i>  18%</i>,  <strong>  4%    </strong>. ",
          " ,  <strong>  22-   </strong> (  ),   <strong>      5%</strong>."
        ]
      }
    ]
  }),
  computed: {},
  beforeCreate() {},
  created() {},
  beforeMount() {},
  mounted() {},
  beforeUpdate() {},
  updated() {},
  beforeDestroy() {},
  methods: {}
};
</script>

<style lang="sass">
	@import './../styles/conf/_sizes.sass'
	@import './../styles/conf/_colors.sass'
	@import './../styles/conf/_breakpoints.sass'
	.ladder

</style>



